/*button menu */
.menuButton,
.article__button {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 4rem;
  height: 4rem;
  right: 0;
  bottom: 0;
  padding: 0;
  margin: 2rem;
  border: 3px solid #1a1a1a;
  background: #fefefe;
  transform: rotate(90deg);
  z-index: 2; }

.menuButton:hover {
  cursor: pointer; }

.menuButton__line {
  font-size: 3rem;
  color: #1a1a1a;
  transform: rotate(45deg); }
  .menuButton__line--on {
    animation-name: line-on;
    animation-duration: .6s;
    animation-fill-mode: both; }
  .menuButton__line--off {
    animation-name: line-off;
    animation-duration: .6s;
    animation-fill-mode: both; }

.menuButton__switch--off {
  animation-name: switch-off;
  animation-duration: .6s;
  animation-fill-mode: both; }

.menuButton__switch--on {
  animation-name: switch-on;
  animation-duration: .6s;
  animation-fill-mode: both; }

.article__button.article__button--position {
  left: 0;
  transform: rotate(0deg); }

.menu-on {
  animation-name: menu-on;
  animation-duration: .6s;
  animation-fill-mode: both; }

.menu-off {
  animation-name: menu-off;
  animation-duration: .6s;
  animation-fill-mode: both; }

.strong__header {
  text-transform: uppercase; }

@keyframes line-off {
  from {
    transform: rotate(135deg);
    color: #efefef; }
  to {
    transform: rotate(45deg);
    color: #1a1a1a; } }

@keyframes line-on {
  from {
    transform: rotate(45deg);
    color: #1a1a1a; }
  to {
    transform: rotate(135deg);
    color: #fff; } }

@keyframes switch-off {
  from {
    background-color: #1a1a1a;
    border-color: #fefefe; }
  to {
    background-color: #fefefe;
    border-color: #1a1a1a; } }

@keyframes switch-on {
  from {
    background-color: #fefefe;
    border-color: #1a1a1a; }
  to {
    background-color: #1a1a1a;
    border-color: #fefefe; } }

@keyframes menu-on {
  from {
    transform: translateX(-100%); }
  to {
    transform: translateX(0); } }

@keyframes menu-off {
  from {
    transform: translateX(0); }
  to {
    transform: translateX(-100%); } }

